import Vue from 'vue';
import VueRouter from 'vue-router';

import DragDrop from '../pages/dragdrop';
import Home from '../pages/home';
import MouseSwiper from '../pages/mouseSwiper';
import Flex from '../pages/flex';
import Exif from '../pages/exif.vue';

Vue.use(VueRouter);

const routes = [{
  path: '/',
  name: 'Home',
  component: Home
},
{
  path: '/DargDorp',
  name: 'DargDorp',
  component: DragDrop
},
{
  path: '/MouseSwiper',
  name: 'MouseSwiper',
  component: MouseSwiper
},
{
  path: '/Flex',
  name: 'Flex',
  component: Flex
},
{
  path: '/Exif',
  name: 'Exif',
  component: Exif
}
];

export default new VueRouter({
  routes // （缩写）相当于 routes: routes
});
